class Shake {
    authorized = false; //
    app = null;

    constructor(isAuth, app) {
        console.log("isAuth ", isAuth);
        this.authorized = !isAuth;
        this.app = app;
        // isAuth=false =  authorized=true
        // isAuth=true  =>  authorized=false
        this.authState();
    }

    //
    authState = () => {
        if (this.authorized) {
            //
            this.shakeListener();
        } else {
            //
            this.askShake();
        }
    }

    //ios 
    askShake() {
        console.log('ios ');
        try {
            window.DeviceMotionEvent.requestPermission()
                .then(response => {
                    if (response == 'granted') {
                        this.authorized = true; //
                        window.addEventListener(
                            'devicemotion',
                            this.deviceMotionHandler,
                            false
                        );
                    }
                })
                .catch(e => {
                    console.log('ios ');
                    console.error(e);
                    this.authorized = false; //
                });
        } catch (ex) {
            console.log('ios ');
            console.error(ex);
            this.authorized = false; //
        }
    }

    //android 
    shakeListener = () => {
        if (window.DeviceMotionEvent) {
            this.authorized = true; //
            window.addEventListener(
                'devicemotion',
                this.deviceMotionHandler,
                false
            );
            console.log('Android,');
        } else {
            console.log('Android,');
            this.authorized = false; //
        }
    }

    //
    deviceMotionHandler = (ev) => {
        const { x, y } = ev.accelerationIncludingGravity;
        this.app.gravityMove({ x, y });
    }
}
export {
    Shake
}